.location-modal {
  max-width: 510px;
  padding: 30px 20px; }
  .location-modal__title {
    font-size: 18px;
    margin-left: -12px;
    margin-right: -12px; }
  .location-modal [class*="btn-"] {
    width: 100%;
    margin-top: 10px; }
  .location-modal .close-button {
    right: 10px;
    top: 4px;
    color: #d0d2d3;
    font-family: "Verdana";
    font-size: 30px; }
  .location-modal p {
    font-size: .9em;
    text-align: justify; }
  @media screen and (min-width: 48em) {
    .location-modal {
      padding: 40px 45px; }
      .location-modal__title {
        font-size: 26px; }
      .location-modal [class*="btn-"] {
        width: 224px;
        display: block;
        margin: 0 auto;
        margin-top: 30px;
        padding-left: 40px;
        padding-right: 40px; }
      .location-modal p {
        font-size: inherit;
        text-align: left; } }
  @media screen and (min-width: 64em) {
    .location-modal__title {
      font-size: 30px; } }
